<template>
  <el-header class="header">
    <el-image src="/assets/logo.png" class="logo"></el-image>
    <div class="nav">
      <router-link v-for="item in navs" :key="item.name" :to="item.path">
        {{ item.name }}
      </router-link>
    </div>
    <div class="space" />
    <div class="end">
      <el-select
        v-if="chainName"
        class="chain-selector"
        :model-value="currentChainId"
        @update:model-value="switchNetwork($event)"
      >
        <el-option
          v-for="item in networkSelectOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
      <el-button type="text" class="lang-btn">Language</el-button>
      <el-tooltip v-if="account">
        <template #content>
          Address: {{ account }} <br /><br />
          Click to disconnected.
        </template>
        <el-button type="text" class="connected-info" @click="disconnect">
          Connected
        </el-button>
      </el-tooltip>
      <span v-else-if="allowConnection">Connecting...</span>
      <el-button
        v-else
        type="primary"
        :disabled="!hasProvider"
        class="connect-btn"
        @click="connect"
      >
        Connect Wallet
      </el-button>
    </div>
  </el-header>
</template>

<script lang="ts" setup>
import { computed } from "vue-demi";
import { useStore } from "vuex";
import { web3Operator } from "../store";
import {
  chainIdToName,
  NetworkNotExistsError,
  UserRejectError,
} from "../store/web3";
import { ElMessage } from "element-plus";

const navs = [
  {
    name: "Home",
    path: "/",
  },
  {
    name: "Publish",
    path: "/publish",
  },
  {
    name: "Collection",
    path: "/collection",
  },
  {
    name: "Wiki",
    path: "/wiki",
  },
  {
    name: "Market",
    path: "/market",
  },
] as { name: string; path: string }[];

const networkSelectOptions = Array.from(chainIdToName).map(([key, value]) => ({
  label: value,
  value: key,
}));

const store = useStore();

async function connect() {
  web3Operator.connect();
}

function disconnect() {
  web3Operator.disconnect();
}

const allowConnection = computed(
  () => store.state.walletConnectionInfo.allowConnection
);
const account = computed(() => store.state.web3.account);
const hasProvider = computed(() => store.getters["web3/hasProvider"]);
const chainName = computed(() => store.getters["web3/chainName"]);
const currentChainId = computed(() => store.state.web3.chainId);

async function switchNetwork(chainId: number) {
  try {
    await store.dispatch("web3/switchNetwork", chainId);
  } catch (error) {
    if (error instanceof NetworkNotExistsError) {
      ElMessage.error(
        `The network ${chainIdToName.get(
          chainId
        )} has not been add to your wallet.`
      );
    } else if (error instanceof UserRejectError) {
      ElMessage.warning(`You have rejected the request.`);
    }
  }
}
</script>

