<template>
  <div class="art-container" v-if="metadata">
    <el-row>
      <el-col :span="8" style="padding-right: 12px">
        <el-card shadow="never" class="area image-area">
          <el-image
            :src="(metadata as INftInformation).urls.cover"
            class="image"
            fit="contain"
          >
            <template #placeholder>
              <div class="image-slot">Loading<span class="dot">...</span></div>
            </template>
          </el-image>
        </el-card>
        <el-card shadow="never" class="area button-area">
          <el-button type="primary">Download</el-button>
          <el-button type="primary" plain style="flex: 1">
            Recieve income ({{ profit }})
          </el-button>
        </el-card>
      </el-col>
      <el-col :span="16" style="padding-left: 12px">
        <el-card shadow="never" class="description-area">
          <div class="top">
            <el-button type="text" class="nftId">#{{ nftId }}</el-button>
            <el-dropdown>
              <el-button :icon="Share"></el-button>
              <template #dropdown>
                <el-dropdown-menu>
                  <el-dropdown-item @click="copyLink"
                    >Use link</el-dropdown-item
                  >
                  <el-dropdown-item disabled>Use poster</el-dropdown-item>
                </el-dropdown-menu>
              </template>
            </el-dropdown>
          </div>
          <Title>{{ metadata.name }}</Title>
          <p class="description">{{ metadata.description }}</p>
          <MetaCard :infomation="metadata as INftInformation"></MetaCard>
        </el-card>
      </el-col>
    </el-row>
  </div>
  <el-skeleton :rows="5" animated v-else />
</template>

<script lang="ts" setup>
import { computed, onMounted, ref, watch } from "vue";
import { useRoute } from "vue-router";
import { Share } from "@element-plus/icons";
import Title from "../components/Title.vue";
import { useStore } from "vuex";
import { UserOperatorFactory } from "@EaseShare/business";
import type { INftInformation } from "@EaseShare/business/generated/src/nftInfomation";
import MetaCard from "../components/art/MetaCard.vue";
const store = useStore();
const factory = computed(
  () => store.getters["web3/userOperatorFactory"] as UserOperatorFactory
);
const infoGetter = computed(() => factory.value?.nftInformationGetter);
const route = useRoute();
const nftId = computed(() => route.params.nftId as string);
const metadata = ref(null as INftInformation | null);
async function resetPage() {
  metadata.value = null;
  if (nftId.value && infoGetter.value) {
    metadata.value = await infoGetter.value.get(nftId.value);
  }
}
watch(() => [nftId.value, infoGetter.value], resetPage);
onMounted(resetPage);

const profit = "10.2 Matic";
const childNum = 12;

const baseUrl = computed(() => store.state.config.frontendBaseUrl);
const shareLink = computed(() => `${baseUrl.value}/#${route.fullPath}`);

function copyLink() {
  const clipboard = navigator.clipboard;
  clipboard.writeText(shareLink.value);
}
</script>

