/* eslint-disable @typescript-eslint/ban-ts-comment */
import type { Module } from "vuex";
import type Web3 from "web3";
// @ts-ignore
import web3 from "web3/dist/web3.min.js";
import { UserOperatorFactory } from "@EaseShare/business";


export interface IWalletConnectionInfo {
  // If this's true, a fresh load of the page should establish connection to wallect automaticly.
  allowConnection: boolean;
}

const walletConnectionInfoOpertor = {
  localStorage: {
    allowConnectionStr: "allowConnection",
    retrieve() {
      const storage = window.localStorage;
      return {
        allowConnection: storage.getItem(this.allowConnectionStr) === "true",
      } as IWalletConnectionInfo;
    },
    store(payload: IWalletConnectionInfo) {
      window.localStorage.setItem(
        this.allowConnectionStr,
        payload.allowConnection.toString()
      );
    },
  },
};

export const walletConnectionInfoStore = {
  namespaced: true,
  state() {
    const data = walletConnectionInfoOpertor.localStorage.retrieve();
    return data;
  },
  mutations: {
    setAllowConnection(state, value) {
      state.allowConnection = value;
    },
  },
  actions: {
    store(context) {
      const state: IWalletConnectionInfo = context.state;
      walletConnectionInfoOpertor.localStorage.store(state);
    },
  },
} as Module<IWalletConnectionInfo, any>;

export const chainIdToName = new Map<number, string>();
chainIdToName.set(1, "ethereum");
chainIdToName.set(4, "rinkeby")
chainIdToName.set(56, "bsc");
chainIdToName.set(137, "matic");

const chainIdToContractAddress = new Map<number, string>();
chainIdToContractAddress.set(4, "0x7bEf2541bd8E5b9927d481cE1BeA408EEbF52323");

export interface IWeb3 {
  account: string;
  chainId: number;
  contractAddress: string;
  web3?: Web3;
  minConfirmationNum: number
}

export const web3Store = {
  namespaced: true,
  state() {
    const givenProvider = (web3 as Web3).givenProvider;
    const data = {
      account: "",
      chainId: -1,
      contractAddress: "",
      minConfirmationNum: 1
    } as IWeb3;
    if (givenProvider) {
      data.web3 = new web3(givenProvider);
    }
    return data;
  },
  mutations: {
    setAccount(state, account: string) {
      if (!state.web3) return;
      state.account = account;
      if (account) state.web3.defaultAccount = account;
      else state.web3.defaultAccount = null;
    },
    setChain(
      state,
      { chainId, contractAddress }: { chainId: number; contractAddress: string }
    ) {
      (state.chainId = chainId), (state.contractAddress = contractAddress);
    },
  },
  getters: {
    hasProvider(state) {
      return !!state.web3;
    },
    chainName(state) {
      if (state.chainId === -1) return "";
      return chainIdToName.get(state.chainId) ?? "other";
    },
    userOperatorFactory(state) {
      const chainName = chainIdToName.get(state.chainId);
      if (
        state.web3 &&
        state.account &&
        chainName &&
        state.chainId &&
        state.contractAddress
      ) {
        return new UserOperatorFactory(
          state.web3,
          state.account,
          chainName,
          state.chainId,
          state.contractAddress,
          state.minConfirmationNum
        );
      }
      return undefined;
    },
  },
  actions: {
    // Request account from provider, and store it in the state.
    async requestAccount(context) {
      if (!context.state.web3) {
        throw new Error("No provider found.");
      }
      const accounts = await context.state.web3.eth.requestAccounts();
      const account = accounts[0];
      if (!account) {
        throw new Error("Account request has failed!");
      }
      context.commit("setAccount", account);
      return account;
    },
    removeAccount(context) {
      context.commit("setAccount", "");
    },
    async fulfillChain(context) {
      if (!context.state.web3) {
        throw new Error("No provider found.");
      }
      const chainId = await context.state.web3.eth.getChainId();
      context.commit("setChain", {
        chainId,
        contractAddress: chainIdToContractAddress.get(chainId) ?? "",
      });
      return chainId;
    },
    removeChain(context) {
      context.commit("setChain", { chainId: -1, contractAddress: "" });
    },
    async switchNetwork(context, chainId: number) {
      if (!context.state.web3) {
        throw new Error("No provider found.");
      }
      const provider = context.state.web3.currentProvider;
      // @ts-ignore
      if (provider?.isMetaMask) {
        try {
          // @ts-ignore
          await provider.request({
            method: "wallet_switchEthereumChain",
            params: [
              {
                chainId: context.state.web3.utils.numberToHex(chainId),
              },
            ],
          });
        } catch (error) {
          const code = (error as { code: number }).code;
          if (code === 4902) throw new NetworkNotExistsError(chainId);
          if (code === 4001) throw new UserRejectError();
          throw error;
        }
      } else {
        throw new Error("The provider can't be recognized.");
      }
      await context.dispatch("fulfillChain");
    }
  },
} as Module<IWeb3, any>;

export class UserRejectError extends Error { }

export class NetworkNotExistsError extends Error {
  chainId: number;
  constructor(chainId: number, msg?: string) {
    super(msg);
    this.chainId = chainId;
  }
}
