<template>
  <el-card shadow="never" class="meta">
    <template #title> Metadata </template>
    <el-descriptions :column="12" border>
      <el-descriptions-item
        v-for="item in displayItems"
        :key="item.label"
        :label="item.label"
        :span="item.span"
        min-width="100"
      >
        {{ item.value }}
      </el-descriptions-item>
    </el-descriptions>
  </el-card>
</template>

<script lang="ts" setup>
import { INftInformation } from "@EaseShare/business/generated/src/nftInfomation";
import { computed } from "vue";
import { useStore } from "vuex";
import type Web3 from "web3";
import { getSymbol } from "../../token";

const props = defineProps<{
  infomation: INftInformation;
}>();
const store = useStore();
const web3 = computed(() => store.state.web3.web3 as Web3);
const tokenAddress = computed(() => props.infomation.paymentCurrency.value);
const chainId = computed(() => store.state.web3.chainId as number);
const symbol = computed(() => getSymbol(chainId.value, tokenAddress.value));
const sellingPrice = computed(() => {
  if (web3.value && props.infomation) {
    return `${web3.value.utils.fromWei(props.infomation.sellingPrice.toString())} ${symbol.value}`;
  }
});

interface IDisplayItem {
  label: string;
  value: string | boolean;
  span: number;
}

const displayItems = computed(() => {
  const information = props.infomation;
  return [
    { label: "Selling Price", value: sellingPrice.value, span: 6 },
    {
      label: "Child node's price baseline",
      value: `${information.baseline}%`,
      span: 6,
    },
    { label: "Content encrypted", value: information.encrypted, span: 6 },
    { label: "Dividen rate", value: information.percentageOfEarnings, span: 6 },
    {
      label: "Allow derivative works",
      value: information.allowSecondaryCreation,
      span: 6,
    },
    { label: "Max sharing times", value: information.maxShareTimes, span: 4 },
  ] as IDisplayItem[];
});
</script>

<style lang="scss" scoped>
.meta {
  border: unset;
  :deep(.el-card__body) {
    padding: unset;
    display: flex;
    flex-direction: column;
    row-gap: 16px;
    align-items: stretch;
  }

  .meta-card {
    cursor: initial;
    & + .meta-card {
      margin-left: unset;
    }
  }
}
</style>
